import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class ContactoService {

  contacto_URL = environment.contacto_URL

  constructor(private httpClient: HttpClient) { }

  //Solicitud Comunicarse con el administrador de la tienda
  public contactoAdmin(createContactoDto: FormData): Observable<any> {
    return this.httpClient.post(this.contacto_URL, createContactoDto);
  }
}
