import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-entrega-mensajeria',
  templateUrl: './entrega-mensajeria.component.html',
  styleUrls: ['./entrega-mensajeria.component.scss']
})
export class EntregaMensajeriaComponent implements OnInit {

  isContentVisibleDatos: boolean = false; // Inicialmente oculto
  isContentVisibleDevolucion: boolean = false; // Inicialmente oculto

  constructor() { }

  ngOnInit(): void {
  }

  toggleContentDatos() {
    this.isContentVisibleDatos = !this.isContentVisibleDatos;
  }

  toggleContentDevolucion() {
    this.isContentVisibleDevolucion = !this.isContentVisibleDevolucion;
  }
}
