import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-blog-right-sidebar',
  templateUrl: './blog-right-sidebar.component.html',
  styleUrls: ['./blog-right-sidebar.component.scss']
})
export class BlogRightSidebarComponent implements OnInit {

  isContentVisibleTerminos: boolean = false; // Inicialmente oculto
  isContentVisibleDatos: boolean = false; // Inicialmente oculto
  isContentVisibleCompras: boolean = false; // Inicialmente oculto



  constructor() { }

  ngOnInit(): void {
  }

  toggleContentTerminos() {
    this.isContentVisibleTerminos = !this.isContentVisibleTerminos;
  }

  toggleContentDatos() {
    this.isContentVisibleDatos = !this.isContentVisibleDatos;
  }

  toggleContentCompras() {
    this.isContentVisibleCompras = !this.isContentVisibleCompras;
  }
}
