import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { ContactoService } from 'src/app/shared/services/contacto.service';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.scss']
})
export class ContactComponent implements OnInit {

  contactForm: FormGroup;

  constructor(
    private fb: FormBuilder,
    private contactoService: ContactoService,
    private toastrService: ToastrService,
  ) {
    // Inicialización del formulario con validaciones básicas
    this.contactForm = this.fb.group({
      con_nombre_cliente: ['', Validators.required],
      con_apellido_cliente: ['', Validators.required],
      con_telefono: ['', [Validators.required, Validators.pattern('^[0-9]*$')]],
      con_email: ['', [Validators.required, Validators.email]],
      con_message: ['', Validators.required]
    });
  }

  ngOnInit(): void {
  }

  // Método para manejar el envío del formulario
  onSubmit() {
    if (this.contactForm.valid) {
      const createContactoDto = this.contactForm.value;

      this.contactoService.contactoAdmin(createContactoDto).subscribe(
        (data) => {
          this.toastrService.success(data.message, 'Éxito', {
            timeOut: 3000,
            positionClass: 'toast-top-center',
          });
          // Reiniciar los inputs
          this.contactForm.reset();

        },
        (error) => {
          const errorMessage = error.error?.message || 'Error al comunicarse con el administrador';
          this.toastrService.error(errorMessage, 'Error', {
            timeOut: 3000,
            positionClass: 'toast-top-center',
          });
          console.error(error);
        }
      );

    } else {
      this.toastrService.error('Ingresa información valida', 'Érror', {
        timeOut: 3000,
        positionClass: 'toast-top-center',
      });
    }
  }

}
